import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import './Questions.css';

function Questions() {
  const [questions, setQuestions] = useState([]);
  const [userAnswers, setUserAnswers] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const query = new URLSearchParams(useLocation().search);
  const role = query.get('role');
  const level = query.get('level');
  const navigate = useNavigate();

  useEffect(() => {
    const fetchQuestions = async () => {
      try {
        const res = await fetch(
          `http://localhost:5000/api/questions/quiz?role=${role}&level=${level}&count=10`
        );
        const data = await res.json();

        if (Array.isArray(data)) {
          setQuestions(data);
        } else {
          setQuestions([]);
          setError('Unexpected response format. No questions found.');
        }
      } catch (err) {
        console.error('Fetch error:', err);
        setError('Failed to load questions. Please try again later.');
      } finally {
        setLoading(false);
      }
    };

    fetchQuestions();
  }, [role, level]);

  const handleOptionClick = (qIndex, optIndex) => {
    setUserAnswers(prev => ({ ...prev, [qIndex]: optIndex }));
  };

  const handleSubmit = () => {
    let score = 0;
    questions.forEach((q, i) => {
      if (userAnswers[i] === q.answerIndex) score++;
    });

    navigate('/score', {
      state: {
        role,
        level,
        score,
        total: questions.length,
        userAnswers,
        questions
      }
    });
  };

  return (
    <div className="questions-wrapper">
      <h2>{role} Test ({level})</h2>

      {loading && <p>Loading questions...</p>}
      {error && <p style={{ color: 'red' }}>{error}</p>}

      {!loading && !error && questions.length === 0 && (
        <div className="empty-state">
          <p>No questions available for this role and level yet.</p>
          <p>Please check back later or try a different combination.</p>
        </div>
      )}

      {!loading && !error && questions.length > 0 && (
        <>
          {questions.map((q, i) => (
            <div key={i} className="question-card">
              <p><strong>Q{i + 1}:</strong> {q.question}</p>
              <ul>
                {q.options.map((opt, idx) => (
                  <li
                    key={idx}
                    className={userAnswers[i] === idx ? 'selected' : ''}
                    onClick={() => handleOptionClick(i, idx)}
                  >
                    {opt}
                  </li>
                ))}
              </ul>
            </div>
          ))}

          <button
            className="submit-button"
            onClick={handleSubmit}
            disabled={Object.keys(userAnswers).length < questions.length}
          >
            Submit Quiz
          </button>
        </>
      )}
    </div>
  );
}

export default Questions;